.control-radio > input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.control-radio > span {
  display: inline-flex;
  align-items: center;
  user-select: none;
  width: 200px;
  margin: 0;
}

.control-radio > span::before {
  content: "";
  display: inline-block;
  width: 42px;
  height: 42px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 2px solid var(--basic-black);
  border-radius: 50%;
  margin-right: 19px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
  left: -32%;
}

.control > input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.control > span {
  display: inline-flex;
  align-items: center;
  user-select: none;
}

.control > span::before {
  content: "";
  display: inline-block;
  width: 38px;
  height: 38px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 2px solid var(--basic-black);
  margin-right: 23px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}

.control > input:checked + span::before {
  background-color: transparent;
  background-image: url("../img/tick.svg");
}

.control-radio > input:checked + span::before {
  background-color: transparent;
  background-image: url("../img/center.svg");
  position: absolute;
}

.control-radio > input + span::before {
  position: absolute;
  top: -3%;
  left: -29%;
}

.control__label {
  font-size: 17px;
  line-height: 18px;
  font-weight: 700;
  margin-bottom: 23px;
}

.control__label-radio {
  font-size: 17px;
  line-height: 18px;
  font-weight: 700;
  position: relative;
}

.control:hover > span::before {
  border: 2px solid var(--tiffany-modal);
}

.control:active > span::before {
  border: 2px solid rgba(68, 68, 68, 0.3);
}

@media (max-width: 767px) {
  .control__label-radio-toy {
    padding-top: 11px;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .control > span::before {
    width: 52px;
    height: 52px;
  }

  .control-radio > span::before {
    width: 50px;
    height: 50px;
  }

  .control-radio > input + span::before {
    position: absolute;
    top: -70%;
    left: -31%;
  }

  .control-radio > input:checked + span::before {
    top: -91%;
  }

  .control__label-radio {
    font-size: 19px;
    line-height: 18px;
    margin-left: 112px;
    display: block;
    margin-top: -2px;
    margin-bottom: 57px;
  }

  .control > span {
    gap: 7px;
    font-size: 19px;
    line-height: 18px;
  }

  .control-radio > span {
    width: 250px;
    white-space: nowrap;
  }
}

@media (min-width: 1150px) {
  .control-radio > span::before {
    width: 52px;
    height: 52px;
  }

  .control > span::before {
    width: 52px;
    height: 52px;
  }

  .control__label-radio {
    font-size: 19px;
    line-height: 18px;
    margin-left: 44px;
  }

  .control-radio > input + span::before {
    top: -90%;
    left: -33%;
  }

  .control__label {
    font-size: 19px;
    line-height: 18px;
    margin-top: 1px;
    margin-left: 0;
    gap: 7px;
  }

  .control__label-radio::before {
    left: 20%;
  }

  .control-radio > span {
    width: 250px;
    white-space: nowrap;
  }
}
