@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open-sans";
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2");
  src: url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open-sans";
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2");
  src: url("../fonts/opensansbold.woff") format("woff");
}
:root {
  --basic-white: #ffffff;
  --basic-black: #000000;
  --basic-tundora: #444444;
  --tiffany-light: #63d1bb;
  --tiffany-dark: #56c3b2;
  --white-smoke: #f1f1f1;
  --light-grey: #f9f9f9;
  --light-smoke: #ececec;
  --tiffany-medium: #46c1ae;
  --tiffany-modal: #62d1ba;
  --light-border: #c6c6c6;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open-sans", "Arial", sans-serif;
  color: var(--basic-tundora);
  background-color: var(--basic-white);
  height: 100%;
  margin: 0;
}

.page__content {
  flex: 1 0 auto;
}

.page-footer {
  flex-shrink: 0;
}

@media (min-width: 320px) and (max-width: 767px) {
  .container {
    margin: 0 auto;
    width: 260px;
  }
}
@media (min-width: 767px) and (max-width: 1149px) {
  .container {
    margin: 0 auto;
    width: 668px;
  }
}
@media (min-width: 1150px) {
  .container {
    margin: 0 auto;
    width: 1050px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.button {
  font-size: 18px;
  line-height: 20px;
  border: 2px solid var(--basic-tundora);
  padding: 16px 70px;
  text-transform: uppercase;
  background-color: var(--basic-white);
  cursor: pointer;
}

.contact {
  font-size: 18px;
  line-height: 20px;
  border: 2px solid var(--basic-tundora);
  padding: 16px 78px;
  text-transform: uppercase;
  background-color: var(--basic-white);
  cursor: pointer;
  color: var(--basic-tundora);
  text-decoration: none;
  font-weight: 700;
  display: block;
}

.title {
  font-size: 25px;
  line-height: 30px;
}

.title-inner {
  padding: 75px 50px;
  background-color: var(-basic-black);
  width: 320px;
  margin: 0 auto;
}

.price {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  display: inline-block;
  text-align: center;
}

.header-page {
  width: 100%;
}

.header-page__content {
  position: relative;
}

.header-page__logo:hover {
  opacity: 0.6;
}
.header-page__logo:active {
  opacity: 0.3;
}

.header-page__toggle-closed {
  background-image: url("../img/menu-button.svg");
  width: 21px;
  height: 20px;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  margin-left: 68px;
  margin-bottom: 8px;
  display: none;
}

.header-page__toggle-opened {
  outline: none;
  border: none;
  background-color: transparent;
  background-image: url("../img/menu-close.svg");
  width: 21px;
  height: 20px;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  display: block;
  margin-left: 68px;
  margin-bottom: 8px;
}

.header-page__link {
  text-transform: none;
  text-decoration: none;
  color: var(--basic-black);
  font-weight: 700;
  position: relative;
}
.header-page__link:hover {
  opacity: 0.6;
}
.header-page__link:active {
  opacity: 0.3;
}

.header-page__list-bottom {
  display: none;
}

.header-page__top {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-top: 22px;
  flex-direction: column;
}

.header-page__free {
  display: none;
}

.header-page__link-search-svg:hover {
  fill: var(--tiffany-light);
  stroke: var(--tiffany-light);
}
.header-page__link-search-svg:active {
  fill: var(--tiffany-light);
  stroke: var(--tiffany-light);
  opacity: 0.6;
}

