.modal__window {
  min-width: 320px;
  max-height: 336px;
  background-color: var(--basic-white);
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  border-top: 1px solid var(--light-border);
}

.modal__title {
  text-transform: uppercase;
  margin-bottom: 14px;
  width: 176px;
  padding-left: 56px;
  white-space: nowrap;
  font-size: 25px;
  line-height: 30px;
  color: var(--basic-black);
