.production__content {
  padding-top: 11px;
  width: 260px;
  position: relative;
  border: 1px solid var(--light-smoke);
  margin-bottom: 59px;
  padding-bottom: 84px;

  &:hover {
    box-shadow: 0 6px 10px 0 rgb(0 0 0 / 10%);
  }

  &:active {
    box-shadow: 0 3px 5px 0 rgb(0 0 0 / 10%);
  }
}

.production-title {
  font-size: 17px;
  line-height: 24px;
  width: 156px;
  margin-left: 91px;
  margin-right: 13px;
  color: var(--basic-black);
  position: relative;
  margin-bottom: 24px;

  &::before {
    content: "";
    display: block;
    position: absolute;
    background-image: url("../img/video.svg");
    width: 32px;
    height: 22px;
    left: -37%;
    top: 20%;
  }
}

.production-desc {
  font-size: 13px;
  line-height: 24px;
  width: 226px;
  margin: 0;
  display: inline-block;
  margin-left: 34px;
  margin-top: 29px;
  margin-bottom: 41px;
}

.production__link {
  width: 170px;
  font-weight: 700;
  padding: 15px 8px;
  text-decoration: none;
  color: var(--basic-black);
  text-align: center;
  font-size: 18px;
  line-height: 20px;
  border: 2px solid var(--basic-tundora);
  text-transform: uppercase;
  background-color: var(--basic-white);
  cursor: pointer;
  position: absolute;
  white-space: nowrap;
  box-sizing: content-box;
  left: 13%;
  top: 83%;

  &:hover {
    background-color: var(--basic-tundora);
    color: var(--basic-white);
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

.production__button {
  content: "";
  display: block;
  position: absolute;
  background-image: url("../img/play.svg");
  width: 100px;
  height: 76px;
  left: 31%;
  top: 27%;
  cursor: pointer;
  border: none;
  background-color: transparent;

  &:hover {
    transform: scale(1.2);
  }

  &:active {
    transform: scale(0.8);
    opacity: 0.3;
  }
}


@media (min-width: 768px) and (max-width: 1149px) {
  .production__content {
    display: flex;
    flex-direction: column;
    width: 668px;
    position: relative;
    padding-top: 0;
    margin-top: 53px;
    padding-bottom: 49px;
    margin-bottom: 65px;
  }

  .production-title {
    order: 2;
    font-size: 20px;
    font-weight: 24px;
    white-space: nowrap;
    margin-top: 34px;
    margin-left: 106px;

    &::before {
      left: -42%;
      top: -4%;
    }
  }

  .production-img {
    order: 1;
    align-self: center;
  }

  .production-desc {
    order: 3;
    font-size: 15px;
    line-height: 30px;
    width: 668px;
    margin: 0;
    margin-bottom: 88px;
    padding-left: 40px;
    padding-right: 40px;
  }

  .production__link {
    order: 4;
    width: 582px;
    position: absolute;
    left: 6%;
    top: 87%;
    padding-right: 0;
    padding-left: 0;
    font-size: 17px;
  }

  .production__button {
    left: 42%;
    top: 25%;
  }
}

@media (min-width: 1150px) {
  .production__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 1050px;
    position: relative;
    margin-bottom: 105px;
    height: 455px;
    padding-top: 0;
    margin-top: 27px;
  }

  .production-img {
    order: 1;
  }

  .production-title {
    font-size: 20px;
    font-weight: 24px;
    order: 2;
    margin-top: 96px;
    margin-left: 78px;

    &::before {
      left: 0%;
      top: -14%;
    }
  }

  .production-desc {
    order: 3;
    position: absolute;
    top: 142px;
    left: 727px;
    font-size: 15px;
    line-height: 30px;
  }

  .production__link {
    order: 4;
    position: absolute;
    top: 76%;
    left: 73%;
    width: 206px;
    padding: 15px 7px;
    font-size: 17px;
    line-height: 16px;
  }

  .production__button {
    left: 28%;
    top: 42%;
  }
}
