.intro {
  background-color: var(--white-smoke);
  position: relative;
}

.intro__title {
  padding: 75px 29px;
  width: 100%;
  white-space: nowrap;
  text-align: center;
  margin: 0;
  color: var(--basic-black);
  margin-bottom: 26px;
  margin-top: 16px;
  font-size: 27px;
  line-height: 30px;
}

@media (min-width: 768px) {
  .intro::before {
    position: absolute;
    display: block;
    content: "";
    background-image: url("../img/zigzag-fill.svg");
    height: 24px;
    background-position: center;
    background-repeat: repeat-x;
    bottom: -1px;
    left: 0;
    width: 100%;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .intro__title {
    padding-top: 111px;
    padding-bottom: 96px;
    font-size: 43px;
    line-height: 50px;
    padding-left: 183px;
    padding-right: 183px;
    margin-bottom: 67px;
    margin-top: 0;
  }
}

@media (min-width: 1150px) {
  .intro {
    position: relative;
  }

  .intro::before {
    background-image: url("../img/zigzag-inner-desktop.svg");
    height: 34px;
    bottom: -10px;
    left: 0;
  }

  .intro__title {
    font-size: 53px;
    font-weight: 57px;
    padding-top: 116px;
    padding-bottom: 113px;
    padding-left: 331px;
    padding-right: 328px;
    margin-top: 0;
  }
}
