.button--order {
  padding: 14px 69px;
  margin-top: 26px;
  font-weight: 700;

  &:hover {
    background-color: var(--basic-tundora);
    color: var(--basic-white);
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .button--order {
    min-width: 292px;
    position: absolute;
    top: 77%;
    left: 0;
    font-size: 17px;
    line-height: 20px;
  }
}

@media (min-width: 1150px) {
  .button--order {
    width: 290px;
    margin-top: 1px;
    margin-left: 9px;
  }
}
