.advantages__title {
  position: relative;
  margin-top: 70px;
  color: var(--basic-black);
}

.advantages__list {
  list-style-type: none;
  font-size: 17px;
  font-weight: 700;
  padding: 0;
  margin-top: 20px;
}

.advantages__item {
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  color: var(--basic-black);
  padding-top: 56px;
  position: relative;
  line-height: 20px;
}


.advantages__item-materials::before {
  background-image: url("../img/icon-1.svg");
  width: 33px;
  height: 38px;
  position: absolute;
  content: "";
  z-index: 5;
  top: -2px;
  left: 8px;
  display: block;
  background-position: center;
  background-repeat: no-repeat;
}

.advantages__item-style::before {
  background-image: url("../img/icon-3.svg");
  width: 47px;
  height: 27px;
  position: absolute;
  content: "";
  z-index: 5;
  top: 1px;
  left: 1px;
  display: block;
  background-position: center;
  background-repeat: no-repeat;
}

.advantages__item-likes::before {
  background-image: url("../img/icon-5.svg");
  width: 34px;
  height: 27px;
  position: absolute;
  content: "";
  z-index: 5;
  top: 5px;
  left: 8px;
  display: block;
  background-position: center;
  background-repeat: no-repeat;
}

.advantages__item-love::before {
  background-image: url("../img/icon-2.svg");
  width: 35px;
  height: 34px;
  position: absolute;
  content: "";
  z-index: 5;
  left: 2px;
  top: 2px;
  display: block;
  background-position: center;
  background-repeat: no-repeat;
}

.advantages__item-support::before {
  background-image: url("../img/icon-4.svg");
  width: 53px;
  height: 23px;
  position: absolute;
  content: "";
  z-index: 5;
  top: 3px;
  left: -5px;
  display: block;
  background-position: center;
  background-repeat: no-repeat;
}

.advantages__item-gift::before {
  background-image: url("../img/icon-6.svg");
  width: 33px;
  height: 32px;
  position: absolute;
  content: "";
  z-index: 5;
  top: 1px;
  left: 3px;
  display: block;
  background-position: center;
  background-repeat: no-repeat;
}


@media (max-width: 767px) {
  .advantages {
    position: relative;

    &::before {
      position: absolute;
      background-image: url("../img/zigzag-line-mobile.svg");
      width: 100%;
      height: 6px;
      content: "";
      display: block;
      left: 0;
      top: 5%;
      background-position: center;
      background-repeat: repeat-x;
    }
  }

  .advantages__item {
    margin-bottom: 28px;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .advantages {
    position: relative;

    &::before {
      position: absolute;
      background-image: url("../img/zigzag-line-desktop.svg");
      width: 100%;
      height: 17px;
      content: "";
      display: block;
      left: 0;
      top: 26px;
      background-position: center;
      background-repeat: repeat-x;
    }
  }

  .advantages__title {
    font-size: 43px;
    line-height: 50px;
    margin-bottom: 42px;
    position: relative;
    margin-top: 100px;
  }

  .advantages__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 57px;
    font-size: 20px;
    line-height: 22px;
  }

  .advantages__item-materials {
    order: 1;
  }

  .advantages__item-love {
    order: 2;
  }

  .advantages__item-style {
    order: 3;
  }

  .advantages__item-support {
    order: 4;
  }

  .advantages__item-likes {
    order: 5;
  }

  .advantages__item-gift {
    order: 6;
  }

  .advantages__item {
    line-height: 21px;
  }
}

@media (min-width: 1150px) {
  .advantages {
    margin-bottom: 70px;
    position: relative;

    &::before {
      position: absolute;
      background-image: url("../img/zigzag-line-desktop.svg");
      width: 100%;
      height: 17px;
      content: "";
      display: block;
      left: 0;
      top: 76px;
    }
  }

  .advantages__content {
    max-width: 675px;
  }

  .advantages__title {
    font-size: 53px;
    line-height: 57px;
    margin-top: 164px;
    margin-left: -12px;
  }

  .advantages__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 11px;
    row-gap: 60px;
    font-size: 20px;
    line-height: 22px;
    margin-top: -14px;
    margin-left: -14px;
  }

  .advantages__item-materials {
    order: 1;
  }

  .advantages__item-love {
    order: 2;
  }

  .advantages__item-style {
    order: 3;
  }

  .advantages__item-support {
    order: 4;
  }

  .advantages__item-likes {
    order: 5;
  }

  .advantages__item-gift {
    order: 6;
  }
}
