*,
*::before,
*::after {
  box-sizing: border-box;
}

$tablet-width: 768px;
$desktop-width: 1150px;
$mobile-width-only: 767px;

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open-sans", "Arial", sans-serif;
  color: var(--basic-tundora);
  background-color: var(--basic-white);
  height: 100%;
  margin: 0;
}

.page__content {
  flex: 1 0 auto;
}

.page-footer {
  flex-shrink: 0;
}

@media (min-width: 320px) and (max-width: 767px) {
  .container {
    margin: 0 auto;
    width: 260px;
  }
}

@media (min-width: 767px) and (max-width: 1149px) {
  .container {
    margin: 0 auto;
    width: 668px;
  }
}

@media (min-width: 1150px) {
  .container {
    margin: 0 auto;
    width: 1050px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.button {
  font-size: 18px;
  line-height: 20px;
  border: 2px solid var(--basic-tundora);
  padding: 16px 70px;
  text-transform: uppercase;
  background-color: var(--basic-white);
  cursor: pointer;
}

.contact {
  font-size: 18px;
  line-height: 20px;
  border: 2px solid var(--basic-tundora);
  padding: 16px 78px;
  text-transform: uppercase;
  background-color: var(--basic-white);
  cursor: pointer;
  color: var(--basic-tundora);
  text-decoration: none;
  font-weight: 700;
  display: block;
}

.title {
  font-size: 25px;
  line-height: 30px;
}

.title-inner {
  padding: 75px 50px;
  background-color: var(-basic-black);
  width: 320px;
  margin: 0 auto;
}

.price {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  display: inline-block;
  text-align: center;
}
