.review__content {
  position: relative;
}

.review__title {
  color: var(--basic-black);
  margin-bottom: 0;
}

.review__text {
  margin: 0;
  line-height: 30px;
  margin-top: -1px;
}

.review__quote {
  margin: 0;
  width: 260px;
  margin-bottom: 32px;
}

.review__author {
  font-weight: 700;
  font-size: 17px;
  line-height: 30px;
  margin-bottom: 28px;
}

.review__nickname {
  font-weight: 400;
  line-height: 24px;
}

.review__buttons {
  display: flex;
  width: 260px;
}

.review__button {
  display: block;
  padding-top: 26px;
  padding-bottom: 26px;
  padding-right: 42px;
  padding-left: 37px;
  margin-bottom: 38px;
  background-color: transparent;
  border: 1px solid var(--light-smoke);
  background-image: url("../img/huge-arrow-left.svg");
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  width: 130px;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.review__button-right {
  background-image: url("../img/huge-arrow-right.svg");

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.reviews-contact {
  width: 260px;
  padding: 12px 78px;

  &:hover {
    background-color: var(--basic-tundora);
    color: var(--basic-white);
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .review {
    background-image: url("../img/review-2.jpg");
    background-position: left;
    background-repeat: no-repeat;
    margin-top: 11px;
    background-position-y: 51px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/review-2@2x.jpg");
      background-size: 350px auto;
    }
  }

  .review__text br {
    display: none;
  }

  .review__title {
    font-size: 43px;
    line-height: 50px;
    margin: 0;
    padding-top: 136px;
    padding-bottom: 22px;
  }

  .reviews-contact {
    position: absolute;
    left: 74%;
    top: 26%;
    width: 175px;
    padding: 14px 36px;
    font-size: 17px;
  }

  .review__quote {
    display: flex;
    flex-direction: column;
    margin-bottom: 41px;
    width: 668px;
    font-size: 17px;
    line-height: 30px;
  }

  .review__buttons {
    position: absolute;
    left: 74%;
    top: 80%;
    width: 100px;
    gap: 11px;
  }

  .review__button {
    border: none;
  }

  .review__nickname {
    padding-bottom: 47px;
    font-size: 17px;
    line-height: 30px;
  }

  .review__author {
    display: flex;
    flex-direction: column;
    font-size: 18px;
    line-height: 24px;
    gap: 5px;
  }
}

@media (min-width: 1150px) {
  .review__content {
    position: relative;
    background-image: url("../img/review-1.jpg");
    background-position: left;
    background-repeat: no-repeat;
    background-size: contain;
    padding-left: 174px;
    background-position-y: 0;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/review-1@2x.jpg");
      background-size: 350px auto;
    }
  }

  .review__title {
    font-size: 53px;
    line-height: 50px;
    margin: 0;
    padding-top: 84px;
    padding-bottom: 21px;
  }

  .review__text {
    font-size: 17px;
    line-height: 30px;
  }

  .review__text br {
    display: none;
  }

  .review__quote {
    display: flex;
    flex-direction: column;
    width: 875px;
  }

  .review__author {
    display: flex;
    flex-direction: column;
    margin-top: -8px;
  }

  .review__nickname {
    padding-bottom: 96px;
  }

  .reviews-contact {
    position: absolute;
    left: 83%;
    top: 17%;
    width: 177px;
    padding: 15px 38px;
  }

  .review__buttons {
    position: absolute;
    left: 84%;
    top: 68%;
    width: 100px;
    gap: 2px;
  }

  .review__button {
    border: none;
  }
}
