.modal__window {
  min-width: 320px;
  max-height: 336px;
  background-color: var(--basic-white);
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  border-top: 1px solid var(--light-border);
}

.modal__title {
  text-transform: uppercase;
  margin-bottom: 14px;
  width: 176px;
  padding-left: 56px;
  white-space: nowrap;
  font-size: 25px;
  line-height: 30px;
  color: var(--basic-black);
  margin-left: 32px;
  margin-top: 29px;
}

.modal__desc {
  line-height: 30px;
  padding-left: 93px;
  margin-bottom: 21px;
}

.modal__sizes {
  display: flex;
  justify-content: center;
  list-style-type: none;
  gap: 7px;
  padding: 0;
  width: 260px;
  margin-left: 30px;
  margin-right: 30px;
  margin-bottom: 30px;
}

.modal__button {
  border: 2px solid rgba(68, 68, 68, 0.1);
  outline: none;
  padding: 17px 34px;
  font-size: 17px;
  line-height: 16px;
  cursor: pointer;
  background-color: transparent;

  &:hover {
    border-color: rgba(68, 68, 68, 0.3);
  }

  &:active {
    border-color: var(--tiffany-modal);
  }
}

.button--modal {
  color: var(--basic-white);
  background-color: var(--tiffany-light);
  font-weight: 700;
  border: none;
  padding: 16px 68px;
  width: 260px;
  margin-left: 30px;
  margin-bottom: 46px;
  font-size: 17px;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background: rgba(255, 255, 255, 0.9);
}

.modal-disabled {
  display: none;
}

@media (min-width: 768px) {
  .modal__window {
    min-width: 566px;
    max-height: 336px;
    background-color: var(--basic-white);
  }

  .modal__title {
    margin: 0 auto;
    padding: 0;
    padding-top: 38px;
    margin-left: 108px;
    font-size: 30px;
    line-height: 57px;
    margin-bottom: 14px;
  }

  .modal__title br {
    display: none;
  }

  .modal__desc {
    margin: 0 auto;
    padding-right: 92px;
    text-align: center;
    font-size: 17px;
    margin-bottom: 18px;
  }

  .modal__sizes {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 31px;
  }

  .modal__button {
    padding: 16px 48px;
  }

  .button--modal {
    padding: 17px 79px;
    position: relative;
    left: 19%;
    margin-bottom: 48px;
    width: 350px;
    margin-left: 1px;
  }
}
