.social__list {
  display: flex;
  list-style-type: none;
  justify-content: center;
  gap: 41px;
  padding-bottom: 23px;
  padding-left: 0;
  position: relative;
  padding-top: 11px;

  &::after {
    display: block;
    position: absolute;
    content: "";
    width: 260px;
    height: 1px;
    background-image: url("../img/divider.svg");
    top: 100%;
  }
}

.social__link-svg:hover {
  fill: var(--tiffany-light);
  stroke: var(--tiffany-light);
}

.social__link-svg:active {
  fill: var(--tiffany-light);
  stroke: var(--tiffany-light);
  opacity: 0.6;
}

@media (min-width: 768px) and (max-width: 1149px) {
  .social__list {
    padding-top: 19px;
  }
}

@media (min-width: 1150px) {
  .social__list {
    margin-top: 6px;
  }

  .social {
    margin-left: 3px;
    margin-top: -10px;
  }
}
