.products__title {
  color: var(--basic-black);
  padding-top: 13px;
  margin-bottom: -12px;
}

.products__subtitle {
  font-size: 17px;
  line-height: 20px;
  color: var(--tiffany-light);
  text-transform: uppercase;
}

.products__info {
  line-height: 20px;
}

.products__img {
  margin-top: 4px;
  margin-bottom: 21px;
  min-width: 260px;
  padding: 0;
}

.products__price {
  padding: 0 68px;
  white-space: nowrap;
  margin: 0;
}

.products__list {
  display: grid;
  grid-template-columns: 97px 159px;
  font-size: 15px;
  line-height: 20px;
  margin-top: 28px;
}

.products__wrapper {
  position: relative;
}

.products__item {
  padding-bottom: 20px;
  position: relative;
  margin-left: 0;
}

.products__item-feature::after {
  position: absolute;
  width: 260px;
  height: 1px;
  left: 0;
  top: 77%;
  content: "";
  display: block;
  background-image: url("../img/table-line.svg");
}

.products__desc {
  line-height: 24px;
}

@media (min-width: 768px) and (max-width: 1149px) {
  .products__block {
    display: grid;
    grid-template-columns: 450px 105px;
    margin-bottom: 37px;
    gap: 102px;
    align-items: end;
  }

  .products__title {
    font-size: 43px;
    line-height: 50px;
    margin-top: 38px;
  }

  .products__subtitle {
    text-align: center;
    line-height: 17px;
    margin: 0;
  }

  .products__desc {
    font-size: 17px;
    line-height: 30px;
    margin-top: 12px;
  }

  .products__wrapper {
    display: grid;
    grid-template-columns: 292px 334px;
    column-gap: 41px;
  }

  .products__list {
    font-size: 15px;
    line-height: 20px;
    white-space: nowrap;
    margin-top: 47px;
  }

  .products__order {
    margin-top: -134px;
  }

  .products__img {
    width: 334px;
    height: 404px;
    margin-top: 23px;
  }

  .products__price {
    font-size: 20px;
    line-height: 24px;
    padding-left: 71px;
  }
}

@media (min-width: 1150px) {
  .products {
    margin-top: 61px;
  }

  .products__block {
    display: grid;
    grid-template-columns: 100px 450px;
    gap: 44px;
    position: relative;
    z-index: 2;
    margin-left: 32px;
  }

  .products__title {
    font-size: 53px;
    line-height: 57px;
    order: 2;
    white-space: nowrap;
  }

  .products__subtitle {
    text-align: center;
    margin-top: 63px;
    font-size: 16px;
    line-height: 19px;
  }

  .products__wrapper {
    margin-left: 16%;
  }

  .products__img {
    width: 525px;
    height: 636px;
    position: absolute;
    top: -29%;
    right: 0;
  }

  .products__desc {
    font-size: 17px;
    line-height: 30px;
    padding-left: 8px;
    padding-top: 19px;
  }

  .products__order {
    display: grid;
    grid-template-columns: 292px;
    gap: 33px;
    margin-top: 44px;
  }

  .products__list {
    margin-top: 59px;
    margin-left: 7px;
    font-size: 16px;
  }

  .products__item {
    padding-bottom: 18px;
  }

  .products__price {
    font-size: 20px;
    line-height: 24px;
    padding-top: 63px;
    padding-left: 81px;
  }
}
