.page-footer {
  background-color: var(--white-smoke);
  width: 100%;
}

.page-footer__logo {
  display: none;
}

.page-footer__logo:hover {
  fill: var(--tiffany-medium);
}

.page-footer__logo:active {
  fill: var(--tiffany-medium);
  opacity: 0.3;
}

@media (min-width: 768px) {
  .page-footer__logo {
    display: block;
  }

  .page-footer__content {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 122px;
  }
}


@media (min-width: 768px) and (max-width: 1149px) {
  .page-footer__content {
    padding-top: 22px;
    gap: 121px;
  }

  .page-footer__logo {
    margin-top: -14px;
  }
}

@media (min-width: 1150px) {
  .page-footer__content {
    padding-top: 43px;
    width: 1050px;
    justify-content: space-between;
  }

  .page-footer__logo {
    margin-top: -36px;
    margin-right: 67px;
  }
}
