.developed-by {
  display: flex;
  padding: 12px 70px;
  gap: 27px;
  align-items: center;
  justify-content: center;
  margin-top: -19px;
}

.developed-by__desc {
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.developed-by__logo {

  fill: var(--tiffany-light);

  &:hover {
    fill: var(--tiffany-medium);
  }

  &:active {
    fill: var(--tiffany-medium);
    opacity: 0.3;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .developed-by {
    padding: 0;
  }
}

@media (min-width: 1150px) {
  .developed-by {
    margin-bottom: 26px;
    margin-right: -66px;
    margin-top: -14px;
  }

  .developed-by__logo {
    margin-top: 4px;
  }
}
