.header-page {
  width: 100%;
}

.header-page__content {
  position: relative;
}

.header-page__logo {
  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.header-page__toggle-closed {
  background-image: url("../img/menu-button.svg");
  width: 21px;
  height: 20px;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  margin-left: 68px;
  margin-bottom: 8px;
  display: none;
}

.header-page__toggle-opened {
  outline: none;
  border: none;
  background-color: transparent;
  background-image: url("../img/menu-close.svg");
  width: 21px;
  height: 20px;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  display: block;
  margin-left: 68px;
  margin-bottom: 8px;
}

.header-page__link {
  text-transform: none;
  text-decoration: none;
  color: var(--basic-black);
  font-weight: 700;
  position: relative;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.header-page__list-bottom {
  display: none;
}


.header-page__top {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-top: 22px;
  flex-direction: column;
}

.header-page__free {
  display: none;
}

.header-page__link-search-svg {
  &:hover {
    fill: var(--tiffany-light);
    stroke: var(--tiffany-light);
  }

  &:active {
    fill: var(--tiffany-light);
    stroke: var(--tiffany-light);
    opacity: 0.6;
  }
}

@media (max-width: 767px) {
  .header-page__list-top-closed {
    list-style-type: none;
    flex-direction: column;
    align-items: center;
    padding: 0;
    border: 1px solid var(--light-smoke);
    margin-bottom: 0;
    display: none;
    position: absolute;
    background-color: var(--basic-white);
    z-index: 10;
    top: 80px;
    margin-top: 0;
  }

  .header-page__list-top-opened {
    list-style-type: none;
    flex-direction: column;
    align-items: center;
    padding: 0;
    border: 1px solid var(--light-smoke);
    margin-bottom: 0;
    display: block;
    position: relative;
    z-index: 0;
    top: 0;
    margin-top: 0;
  }


  .header-page__content {
    margin: 0 auto;
  }

  .header-page__logo {
    margin-left: 87px;
  }

  .header-page__item {
    box-shadow: 0 1px 0 0 rgba(236, 236, 236, 1);
    text-align: center;
    width: 100%;
    position: relative;
    white-space: nowrap;
    padding: 29px 118px 26px 76px;
  }

  .header-page__top-logo {
    display: flex;
    align-items: center;
    margin: 0 auto;
    justify-content: center;
  }

  .header-page__nav {
    width: 100%;
  }

  .header-page__link-cart::before {
    position: absolute;
    background-image: url("../img/cart.svg");
    width: 21px;
    height: 18px;
    content: "";
    display: block;
    left: -47px;
    top: 14%;
  }

  .header-page__link-search-svg {
    display: block;
    height: 18px;
    content: "";
    position: absolute;
    left: -45px;
    top: 8%;
  }

  .header-page__site-nav,
  .header-page__user-nav {
    list-style-type: none;
    padding: 0;
    width: 100%;
    font-size: 17px;
    line-height: 24px;
    box-shadow: 0 1px 0 0 rgba(236, 236, 236, 1);
    margin-right: -28px;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .header-page {
    position: relative;
    border-bottom: 1px solid var(--light-smoke);
  }

  .header-page__toggle {
    display: none;
  }

  .header-page__content {
    margin: 0 auto;
    width: 668px;
  }

  .header-page__site-nav {
    position: absolute;
    display: flex;
    left: 0;
    padding: 0;
    top: 124%;
    gap: 36px;
    z-index: 5;
  }

  .header-page__user-nav {
    display: flex;
    align-items: flex-start;
    gap: 31px;
  }

  .header-page__logo {
    display: block;
    cursor: pointer;
    margin-bottom: 10px;
  }


  .header-page__title-hidden {
    display: none;
  }

  .header-page__link-search-svg {
    display: block;
    width: 18px;
    height: 18px;
    margin-bottom: 14px;
  }

  .header-page__link-cart::before {
    position: absolute;
    background-image: url("../img/cart.svg");
    width: 21px;
    height: 18px;
    content: "";
    display: block;
    left: -28%;
    top: 3%;
  }

  .header-page__link-cart:hover::before {
    background-image: url("../img/cart-hover.svg");
  }

  .header-page__link-cart:active::before {
    background-image: url("../img/cart-active.svg");
  }

  .header-page__link-cart {
    margin-left: 85px;
    font-size: 17px;
    line-height: 24px;
  }

  .header-page__list-top {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 31px;
    margin-top: 7px;
  }

  .header-page__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    white-space: nowrap;
    height: 88px;
    flex-direction: row;
  }

  .header-page__nav {
    display: block;
  }

  .header-page__item {
    list-style-type: none;
    padding-left: 0;
  }

  .header-page__link {
    list-style-type: none;
    text-decoration: none;
    color: var(--basic-black);
    font-weight: 700;
  }

  .header-page__free {
    display: block;
    text-decoration: none;
    color: var(--basic-black);
    position: absolute;
    top: 106%;
    right: 0;
    z-index: 5;
  }
}

@media (min-width: 1150px) {
  .header-page {
    position: relative;
    border-bottom: 1px solid var(--light-smoke);
  }

  .header-page__content {
    margin: 0 auto;
    width: 1050px;
  }

  .header-page__site-nav {
    display: flex;
    margin-bottom: 10px;
    gap: 39px;
    padding: 0;
    margin-left: 26px;
    position: absolute;
    left: -5%;
    top: 38%;
  }

  .header-page__user-nav {
    display: flex;
    padding-left: 282px;
    gap: 80px;
    align-items: center;
    margin-bottom: 10px;
  }

  .header-page__toggle {
    display: none;
  }

  .header-page__logo {
    display: block;
    cursor: pointer;
    position: absolute;
    margin: 0 auto;
    top: 10%;
    left: 43%;
    width: 149px;
    height: 72px;
  }

  .header-page__title-hidden {
    display: none;
  }

  .header-page__list-top {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 193px;
    padding: 0;
    font-size: 17px;
    line-height: 24px;
  }

  .header-page__link-search {
    padding-left: 32px;
    padding-right: 35px;
    border-left: 1px solid var(--light-smoke);
    border-right: 1px solid var(--light-smoke);
    padding-top: 32px;
    padding-bottom: 34px;
  }

  .header-page__link-search-svg {
    width: 18px;
    height: 18px;
    margin-top: 5px;
  }

  .header-page__link-cart::before {
    position: absolute;
    background-image: url("../img/cart.svg");
    width: 21px;
    height: 18px;
    content: "";
    display: block;
    left: -52px;
    top: 2px;
  }

  .header-page__link-cart:hover::before {
    background-image: url("../img/cart-hover.svg");
  }

  .header-page__link-cart:active::before {
    background-image: url("../img/cart-active.svg");
  }

  .header-page__top {
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
    height: 88px;
  }

  .header-page__nav {
    display: block;
  }

  .header-page__item {
    list-style-type: none;
    padding-left: 0;
  }

  .header-page__link {
    list-style-type: none;
    text-decoration: none;
    color: var(--basic-black);
    font-weight: 700;
  }

  .header-page__link-order {
    text-decoration: none;
  }

  .header-page__free {
    display: block;
    text-decoration: none;
    color: var(--basic-black);
    position: absolute;
    top: 106%;
    right: 0;
    z-index: 5;
  }

  .header-page__list-bottom {
    display: flex;
    position: absolute;
    z-index: 5;
    top: 106%;
    left: -39px;
    font-size: 15px;
    line-height: 24px;
    gap: 62px;
  }

  .header-page__link-catalog {
    margin-right: 33px;
  }

  .header-page__item-cart {
    margin-right: -133px;
  }
}
