.contacts {
  position: relative;
}

.contacts__title {
  position: relative;
  margin-top: 89px;
  color: var(--basic-black);
  margin-bottom: 31px;
}

.contacts__list {
  display: flex;
  list-style-type: none;
  padding: 0;
  gap: 29px;
  flex-direction: column;
  font-size: 14px;
  line-height: 30px;
  margin-bottom: 62px;
}

.contacts__item-email {
  margin-left: 30px;
  list-style-type: none;
  color: var(--basic-black);
  text-decoration: none;
  cursor: pointer;

  &:hover {
    color: var(--tiffany-medium);
  }

  &:active {
    border-bottom: 1px solid rgba(70, 193, 174, 0.1);
  }
}

.contacts__item-address {
  display: flex;
}

.contacts__item-location {
  margin-left: 35px;
  display: block;
}

iframe {
  display: block;
  border: none;
  outline: none;
}

.contacts__map-link {
  width: 230px;
  line-height: 20px;
  font-size: 18px;
  padding: 16px 39px;
  margin-top: 85px;
  margin-bottom: 38px;
  white-space: nowrap;
  margin-left: 0;

  &:hover {
    background-color: var(--basic-tundora);
    color: var(--basic-white);
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

@media (max-width: 767px) {
  .contacts {
    position: relative;
    padding-bottom: 136px;

    &::before {
      position: absolute;
      background-image: url("../img/zigzag-line-mobile.svg");
      width: 100%;
      height: 6px;
      content: "";
      display: block;
      left: 0;
      top: 5%;
      background-position: center;
      background-repeat: repeat-x;
    }
  }


  .contacts__map-link {
    padding: 16px 50px;
    width: 260px;
    white-space: nowrap;
    margin: 43px auto 38px;
    position: absolute;
    bottom: 3px;
    line-height: 20px;
    font-size: 18px;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .contacts {
    margin-top: 39px;
    position: relative;

    &::before {
      position: absolute;
      background-image: url("../img/zigzag-line-desktop.svg");
      width: 100%;
      height: 17px;
      content: "";
      display: block;
      left: 0;
      top: 0;
      background-position: center;
      background-repeat: repeat-x;
    }
  }

  .contacts__content {
    padding-bottom: 106px;
  }

  .contacts__title {
    font-size: 43px;
    line-height: 50px;
    margin-bottom: 47px;
    position: relative;

    &::after {
      position: absolute;
      display: block;
      background-repeat: no-repeat;
      background-position: center;
      background-image: url("../img/flag.svg");
      width: 47px;
      height: 80px;
      right: 3px;
      top: -49%;
      content: "";
    }
  }

  .contacts__list {
    display: flex;
    flex-direction: row;
    margin-bottom: 49px;
    font-size: 17px;
    line-height: 30px;
    gap: 52px;
    position: relative;

    &::before {
      position: absolute;
      content: "";
      background-image: url("../img/map-pin.svg");
      width: 67px;
      height: 100px;
      display: block;
      top: 304%;
      left: 47%;
      z-index: 10;
    }
  }

  .contacts__item {
    font-size: 17px;
    line-height: 30px;
    position: relative;
  }

  .contacts__item-post {
    display: flex;
    white-space: nowrap;
    gap: 3px;
  }

  .contacts__item-email {
    margin-left: 21px;
  }

  .contacts__item-location {
    margin-left: 24px;
  }

  .contacts__map-link {
    width: 230px;
    margin: 0 auto;
    font-size: 17px;
    padding: 14px 38px;
    margin-bottom: 57px;
    white-space: nowrap;
    position: absolute;
    bottom: -1px;
    left: 35%;
    line-height: 20px;
  }
}

@media (min-width: 1150px) {
  .contacts {
    position: relative;
    margin-top: 150px;

    &::before {
      position: absolute;
      background-image: url("../img/zigzag-line-desktop.svg");
      width: 100%;
      height: 17px;
      content: "";
      display: block;
      left: 0;
      top: -97px;
    }
  }

  .contacts__content {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    position: relative;
    margin-bottom: 63px;
  }

  .contacts__details {
    padding-left: 174px;
  }

  .contacts__title {
    font-size: 53px;
    line-height: 57px;
    position: relative;
    margin-top: 67px;
    margin-bottom: 36px;

    &::after {
      position: absolute;
      display: block;
      background-repeat: no-repeat;
      background-position: center;
      background-image: url("../img/flag.svg");
      width: 47px;
      height: 80px;
      left: -111px;
      top: -33%;
      content: "";
    }
  }

  .contacts__list {
    width: 312px;
    font-size: 17px;
  }

  .contacts__item-email {
    margin-left: 21px;
    border-bottom: 1px solid var(--tiffany-medium);
    padding-bottom: 4px;
  }

  .contacts__item-location {
    margin-left: 26px;
  }


  .contacts__map-link {
    font-size: 17px;
    margin-top: 81px;
  }
}
