.form {
  margin-top: -12px;
  margin-bottom: 43px;
}

.form-desc {
  font-size: 13px;
  line-height: 24px;
}

.form__title {
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: left;
  color: var(--tiffany-light);
  margin-top: 17px;
}

.form__checkbox-radio {
  padding: 0;
  margin-top: 29px;
  display: flex;
  flex-direction: column;
  margin-bottom: 38px;
  margin-left: 58px;
  gap: 15px;
}


.form__list {
  padding: 0;
  margin-top: 27px;
  margin-bottom: -7px;
}

.form__item {
  list-style-type: none;
  padding: 0;
}

.form__label {
  font-size: 17px;
  line-height: 18px;
  font-weight: 700;
  margin-top: 6px;
  margin-left: -2px;
}

.form__fieldset {
  display: flex;
  flex-direction: column;
  border: none;
  padding-left: 0;
}

.form__input {
  border-left: 0;
  border-right: 0;
  border-top: 0;
  padding-bottom: 16px;
  padding-top: 17px;
  font-size: 17px;
  line-height: 18px;
  font-weight: 400;
  width: 257px;
  margin-left: -2px;
  border-bottom: 2px solid var(--basic-black);
  opacity: 1;

  &:hover {
    border-bottom: 2px solid var(--tiffany-modal);
  }

  &:focus {
    border-bottom: 2px solid var(--tiffany-modal);
    outline: none;
  }
}

.form__additional {
  min-height: 142px;
  border: 2px solid var(--basic-black);
  margin-top: 21px;
  margin-left: 0;
  padding-left: 13px;

  &:hover {
    border: 2px solid var(--tiffany-modal);
  }

  &:active {
    border: 2px solid var(--tiffany-modal);
  }

  &:focus {
    border: 2px solid var(--tiffany-modal);
  }
}

.form__input::placeholder {
  opacity: 0.3;
}

.form__title,
.form__label-contacts {
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: left;
  color: var(--tiffany-light);
}

.form__subtitle {
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 34px;
  margin-top: 6px;
  display: block;
}

.form__button-order {
  font-weight: 700;
  padding: 15px 45px;
  margin-bottom: 7px;
  font-size: 17px;

  &:hover {
    background-color: var(--basic-tundora);
    color: var(--basic-white);
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

.form__contact,
.form__input,
.form__option {
  position: relative;
}

.form__contact {
  margin-top: 19px;
  position: relative;

  &::after {
    position: absolute;
    background-image: url("../img/phone.svg");
    width: 21px;
    height: 22px;
    content: "";
    display: block;
    right: 4%;
    top: 44%;
  }
}

.form__contact-email {
  position: relative;
  margin-top: 20px;

  &::after {
    position: absolute;
    background-image: url("../img/mail.svg");
    width: 32px;
    height: 21px;
    content: "";
    display: block;
    right: 1%;
    top: 49%;
  }
}

.form__option {
  margin-top: 22px;
}

.form__order {
  margin-bottom: 49px;
}

input::placeholder {
  font-size: 17px;
  line-height: 18px;
  font-family: "Open-sans", "Arial", sans-serif;
}

.form__additional::placeholder {
  font-size: 17px;
  line-height: 18px;
  font-family: "Open-sans", "Arial", sans-serif;
}

.form__input-contacts-phone {
  padding-top: 20px;
  padding-bottom: 17px;
}

.form__input-contact-email {
  padding-top: 22px;
  padding-bottom: 10px;
}

.form__input-contact-phone {
  padding-top: 21px;
}


@media (max-width: 767px) {
  .form__label-contacts {
    position: relative;
  }

  .form__label-contacts::before {
    position: absolute;
    display: block;
    content: "";
    background-image: url("../img/line-form.svg");
    left: 23%;
    top: 45%;
    width: 202px;
    height: 1px;
  }

  .form__label-contacts-email::before {
    position: absolute;
    display: block;
    content: "";
    background-image: url("../img/line-form.svg");
    left: 30%;
    top: 46%;
    width: 182px;
    height: 1px;
  }

  .form__title {
    position: relative;
  }

  .form__title::before {
    position: absolute;
    display: block;
    content: "";
    background-image: url("../img/line-form.svg");
    left: 23%;
    top: 45%;
    width: 202px;
    height: 1px;
  }
}


@media (min-width: 768px) and (max-width: 1149px) {
  .form__background {
    background-color: var(--light-grey);
  }

  .form__checkbox-radio {
    padding: 0;
    margin-top: 25px;
    margin-left: 115px;
    display: flex;
    flex-direction: column;
    gap: 51px;
    margin-bottom: 64px;
  }

  .form-desc {
    width: 668px;
    margin: auto;
    margin-top: 68px;
    margin-bottom: 63px;
    font-size: 17px;
    line-height: 30px;
  }

  .form__title {
    margin-top: 29px;
  }

  .form__type {
    display: grid;
    grid-template-columns: 20% 80%;
  }

  .form__color {
    display: grid;
    grid-template-columns: 20% 80%;
    background-color: var(--light-grey);
  }

  .form__contact {
    background-color: var(--light-grey);

    &::after {
      right: 4%;
      top: 54%;
    }
  }

  .form__contact-email::after {
    right: 3%;
    top: 8%;
  }

  .form__list {
    display: grid;
    grid-template-columns: 200px 200px;
    column-gap: 62px;
    margin-left: 33px;
    margin-top: 50px;
    margin-bottom: 28px;
  }

  .form__fieldset {
    width: 458px;
    margin-left: 138px;
    display: flex;
    flex-direction: row;
    gap: 28px;
  }

  .form__label-contacts {
    position: absolute;
    left: 0;
  }

  .form__order {
    display: flex;
    gap: 109px;
    margin-top: 45px;
    margin-bottom: 0;
  }

  .form__button-order {
    order: 1;
    padding: 14px 22px;
    margin-bottom: 13px;
    margin-top: 1px;
    width: 230px;
  }

  .form__subtitle {
    order: 2;
    margin-top: 19px;
    margin-left: 29px;
    font-size: 17px;
    white-space: nowrap;
  }

  .form__input-contact {
    background-color: transparent;
    margin-top: 42px;
    margin-left: 31px;
    text-indent: 14px;
    width: 499px;
    position: relative;
  }

  .form__title-color {
    margin-top: 67px;
  }

  .form__title-fullname {
    margin-top: 129px;
    margin-left: 1px;
    margin-bottom: 5px;
  }

  .form__label {
    font-size: 19px;
    line-height: 18px;
  }

  .form__fullname {
    margin-top: -35px;
    margin-left: 31px;
  }

  input::placeholder {
    font-size: 19px;
    line-height: 18px;
    font-family: "Open-sans", "Arial", sans-serif;
  }

  .form__input-name {
    margin-top: -8px;
    margin-left: -3px;
    text-indent: 16px;
    width: 428px;
  }

  .form__label-fam {
    margin-left: 29px;
    margin-top: 25px;
  }

  .form__input-fam {
    margin-top: 8px;
    margin-left: -2px;
    text-indent: 14px;
    width: 376px;
    padding-top: 19px;
    padding-bottom: 13px;
  }

  .form__label-ot {
    margin-top: 25px;
    margin-left: 29px;
  }

  .form__input-ot {
    margin-top: 8px;
    padding-top: 19px;
    text-indent: 16px;
    padding-bottom: 14px;
    width: 376px;
  }

  .form__label-contacts-phone {
    margin-top: 45px;
    margin-left: 2px;
    padding-top: 17px;
    padding-bottom: 17px;
  }

  .form__name {
    margin-bottom: 40px;
    margin-top: -60px;
  }

  .form__label-contacts-email {
    top: 10%;
    left: -2px;
    margin-left: 3px;
  }

  .form__input-contact-email {
    margin-top: -18px;
    padding-top: 20px;
    padding-bottom: 17px;
    text-indent: 17px;
    margin-bottom: 36px;
  }

  .form__contact-email {
    margin-top: 22px;
  }

  .form__option {
    margin-top: 41px;
  }

  .form__label-contacts-extra {
    margin-top: 65px;
    margin-left: 2px;
  }

  .form__additional {
    padding-left: 1px;
  }

  .form__additional::placeholder {
    font-size: 19px;
    line-height: 18px;
    font-family: "Open-sans", "Arial", sans-serif;
    text-indent: 13px;
    padding-top: 0;
  }

  .form {
    margin-bottom: 34px;
  }
}

@media (min-width: 1150px) {
  .form__background {
    background-color: var(--light-grey);
  }

  .form-title {
    font-size: 53px;
    line-height: 57px;
    padding-top: 101px;
    padding-bottom: 96px;
    position: relative;

    &::before {
      position: absolute;
      display: block;
      content: "";
      background-image: url("../img/zigzag-fill-desktop.svg");
      width: 1150px;
      height: 34px;
      background-position: center;
      background-repeat: no-repeat;
      top: 93%;
      left: 0;
      z-index: 5;
    }
  }

  .form-desc {
    margin: 124px auto 63px;
    font-size: 17px;
    line-height: 30px;
    width: 702px;
  }

  .form__type {
    display: grid;
    grid-template-columns: 20% 80%;
  }

  .form__color {
    display: grid;
    grid-template-columns: 20% 80%;
    background-color: var(--light-grey);
    margin-top: 29px;
    padding-bottom: 34px;
  }

  .form__title {
    margin-top: 30px;
  }

  .form__list {
    display: grid;
    grid-template-columns: 250px 250px 250px;
    padding: 0;
    margin-top: 48px;
    margin-left: -35px;
    column-gap: 13px;
  }

  .form__contact {
    background-color: var(--light-grey);
    margin-top: 39px;
  }


  .form__fieldset {
    width: 573px;
    margin-left: 138px;
    display: flex;
    flex-direction: row;
    gap: 72px;
    align-items: center;
  }

  .form__input {
    width: 573px;
  }

  .form__label-contacts {
    position: absolute;
    left: 0;
    top: 61%;
    margin-left: 63px;
    z-index: 10;
  }

  .form__order {
    display: flex;
    gap: 169px;
    margin-top: 38px;
    width: 700px;
    margin-bottom: -5px;
  }

  .form__button-order {
    order: 1;
    width: 230px;
    padding: 14px 31px;
    margin-bottom: 20px;
    white-space: nowrap;
  }

  .form__subtitle {
    order: 2;
    font-size: 17px;
    line-height: 30px;
    white-space: nowrap;
    margin-top: 13px;
  }

  .form__input-contact {
    width: 700px;
    background-color: transparent;
    margin-top: 47px;
    margin-left: 40px;
    text-indent: 15px;
    padding-top: 17px;
    z-index: 1;
  }

  .form__input-name {
    width: 626px;
    text-indent: 16px;
    padding-top: 19px;
    padding-bottom: 15px;
    margin-left: -12px;
  }

  .form__input-ot {
    margin-left: -11px;
    margin-top: 8px;
    padding-top: 19px;
    text-indent: 15px;
    padding-bottom: 12px;
  }

  .form__label {
    margin-right: -35px;
    font-size: 19px;
    line-height: 18px;
    margin-top: -1px;
  }

  .form__title-color {
    margin-top: 67px;
  }

  .form__checkbox-radio {
    display: flex;
    flex-direction: column;
    gap: 52px;
    margin-top: 25px;
    margin-left: 47px;
    margin-bottom: 34px;
  }

  .form__name {
    margin-top: 70px;
  }

  .form__fullname {
    margin-top: -59px;
    margin-left: 40px;
  }

  input::placeholder {
    font-size: 19px;
    line-height: 18px;
    font-family: "Open-sans", "Arial", sans-serif;
  }

  .form__label-fam {
    margin-left: 38px;
    margin-top: 9px;
  }

  .form__input-fam {
    margin-left: -11px;
    margin-top: 8px;
    text-indent: 14px;
    padding-top: 18px;
    padding-bottom: 15px;
    width: 573px;
  }

  .form__label-ot {
    margin-left: 39px;
    margin-top: 10px;
  }

  .form__input-contacts-phone {
    margin-top: -40px;
    padding-top: 18px;
  }

  .form__contact-email {
    padding: 0;
    margin-top: -33px;
    padding-bottom: 44px;
  }

  .form__option {
    margin-top: 43px;
  }

  .form__label-contacts-extra {
    top: 16%;
  }

  .form__label-contacts-email {
    top: 44%;
  }

  .form__additional::placeholder {
    font-size: 19px;
    line-height: 18px;
    font-family: "Open-sans", "Arial", sans-serif;
  }

  .form__additional {
    margin: 0;
    margin-left: 39px;
    padding-top: 13px;
    text-indent: 2px;
  }

  .form {
    margin-bottom: 28px;
  }

  .form__title,
  .form__label-contacts {
    margin-left: 62px;
  }

  .form__contact::after {
    right: 18%;
    top: 56%;
    z-index: 5;
  }

  .form__contact-email::after {
    right: 17%;
    top: 42%;
  }
}
