.map__desktop {
  display: none;
}

.map__tablet {
  display: none;
}

@media (max-width: 767px) {
  .map {
    margin-left: -30px;
    position: relative;

    &::before {
      position: absolute;
      background-image: url("../img/map-pin.svg");
      width: 67px;
      height: 100px;
      content: "";
      display: block;
      left: 48%;
      top: 32%;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .map__mobile {
    display: block;
    margin: 0 auto;
    margin-bottom: 55px;
    margin-left: -50px;
    position: relative;
    width: 768px;
    height: 457px;
  }
}


@media (min-width: 1150px) {
  .map__mobile {
    display: block;
    margin: 0 auto;
    margin-bottom: 55px;
    position: absolute;
    top: -3%;
    right: 0%;
    width: 527px;
    height: 457px;
  }
}
