.main__title {
  font-size: 27px;
  line-height: 30px;
  text-align: center;
  padding-top: 71px;
  padding-bottom: 120px;
  color: var(--basic-black);
  margin-bottom: 0;
  margin-top: 0;
}

.main__title-wrapper {
  background-image: url("../img/hero-mobile.jpg");
  padding: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-color: var(--light-grey);
  width: 100%;
  background-position-y: -18px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 320px auto;
  }
}

.main__list-type {
  padding: 0;
  margin: 0;
}

.main__item {
  text-decoration: none;
  color: var(--basic-white);

  &:hover span.main__item-hover {
    margin-top: -5px;
    display: block;
  }
}

.main__item--interior {
  background-color: var(--tiffany-light);
  color: var(--basic-white);
  padding-left: 30px;
  padding-top: 24px;
  padding-bottom: 31px;
  font-weight: 700;
  position: relative;
  font-size: 19px;
  line-height: 24px;

  &:active.main__item--interior::after {
    opacity: 0.3;
  }

  &:hover {
    box-shadow: 0 -5px 0 0 rgba(99, 209, 187, 1);
  }
}

.main__item--interior::after {
  display: block;
  content: "";
  position: absolute;
  background-image: url("../img/interior-page.svg");
  width: 40px;
  height: 62px;
  top: 23px;
  right: 36px;
}

.main__item--toys {
  background-color: var(--tiffany-dark);
  color: var(--basic-white);
  padding-left: 30px;
  padding-top: 24px;
  padding-bottom: 31px;
  font-weight: 700;
  position: relative;
  font-size: 19px;
  line-height: 24px;

  &::after {
    display: block;
    content: "";
    position: absolute;
    background-image: url("../img/toy-page.svg");
    width: 60px;
    height: 60px;
    top: 23px;
    right: 27px;
  }

  &:active.main__item--toys::after {
    opacity: 0.3;
  }

  &:hover {
    box-shadow: 0 -5px 0 0 rgba(86, 195, 178, 1);
  }
}

@media (max-width: 767px) {
  .main__content {
    margin: 0 auto;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .main {
    position: relative;
    background-color: var(--light-grey);
  }

  .main__content {
    margin: 0 auto;
    width: 768px;
  }

  .main__title br {
    display: none;
  }

  .main-page::after {
    position: absolute;
    display: block;
    content: "";
    background-image: url("../img/zigzag-fill.svg");
    width: 100%;
    height: 24px;
    background-position: center;
    background-repeat: repeat-x;
    bottom: -10px;
    left: 0;
    z-index: 5;
  }

  .main__title {
    text-align: center;
    padding-bottom: 372px;
    font-size: 43px;
    line-height: 50px;
    margin: 0;
    padding-top: 157px;
    padding-left: 107px;
    margin-right: 108px;
  }

  .main__title-wrapper {
    background-image: url("../img/hero-tablet.jpg");
    background-color: var(--light-grey);
    width: 100%;
    background-position-y: -1px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
      background-size: 768px auto;
    }
  }

  .main__list-type {
    display: grid;
    grid-template-columns: 334px 334px;
    list-style-type: none;
    padding: 0;
    font-size: 20px;
    line-height: 30px;
    margin: 0;
    position: relative;
    justify-content: center;
    margin-top: -3px;
  }

  .main__item--interior {
    padding-left: 37px;
    padding-top: 28px;
    padding-bottom: 37px;
    width: 334px;
    font-size: 20px;
    line-height: 28px;

    &::after {
      top: 30px;
    }
  }

  .main__item--toys {
    padding-left: 37px;
    padding-top: 28px;
    padding-bottom: 37px;
    width: 334px;
    font-size: 20px;
    line-height: 28px;

    &::after {
      right: 33px;
      top: 31px;
    }
  }
}

@media (min-width: 1150px) {
  .main {
    position: relative;
    background-color: var(--light-grey);
  }

  .main__content {
    margin: 0 auto;
    width: 1150px;
  }

  .main__title br {
    display: none;
  }

  .main-page::after {
    position: absolute;
    display: block;
    content: "";
    background-image: url("../img/zigzag-fill-desktop.svg");
    width: 100%;
    height: 34px;
    background-position: center;
    background-repeat: repeat-x;
    bottom: -4px;
    left: 0;
    z-index: 5;
  }

  .main__title {
    font-size: 53px;
    line-height: 57px;
    padding: 0 225px;
    text-align: center;
    padding-top: 157px;
    padding-bottom: 355px;
    width: 100%;
    margin: 0 auto;
  }

  .main__title-wrapper {
    background-image: url("../img/hero-desktop.jpg");
    background-color: var(--light-grey);
    width: 100%;
    background-position-y: 1px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
      background-size: 1150px auto;
    }
  }

  .main__list-type {
    display: grid;
    grid-template-columns: 525px 525px;
    list-style-type: none;
    font-size: 20px;
    line-height: 30px;
    font-weight: 700;
    margin-top: 0;
    position: relative;
    margin-bottom: 0;
    justify-content: center;
  }

  .main__item--interior {
    padding-left: 37px;
    padding-top: 25px;
    padding-bottom: 49px;
    width: 525px;
    font-size: 20px;
    line-height: 30px;
  }

  .main__item--interior::after {
    top: 29px;
  }

  .main__item--toys {
    padding-left: 37px;
    padding-top: 25px;
    padding-bottom: 49px;
    width: 525px;
    font-size: 20px;
    line-height: 30px;
  }

  .main__item--toys::after {
    top: 30px;
    right: 35px;
  }

  .main__list-bottom {
    display: flex;
    position: absolute;
    left: -1%;
    top: 13%;
  }
}
