.catalog__list {
  padding: 0;
}

.catalog__subtitle {
  margin-bottom: 25px;
}

.catalog__item {
  list-style-type: none;
  padding: 0;
  margin: 0 auto;
  border: 1px solid var(--light-smoke);
  margin-bottom: 29px;

  &:hover {
    box-shadow: 0 6px 10px 0 rgb(0 0 0 / 10%);
  }

  &:active {
    box-shadow: 0 3px 5px 0 rgb(0 0 0 / 10%);
  }
}

.catalog__item-link {
  text-decoration: none;
}

.catalog__item-title {
  font-size: 17px;
  line-height: 24px;
  margin-left: 28px;
  color: var(--basic-black);
  margin-bottom: 6px;
  margin-top: 23px;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.catalog__cart-svg {
  opacity: 0.2;

  &:hover {
    fill: var(--tiffany-light);
    opacity: 1;
  }

  &:active {
    fill: var(--tiffany-light);
    opacity: 0.6;
  }
}

.catalog__desc {
  font-size: 13px;
  line-height: 30px;
  margin-left: 28px;
  margin-bottom: 27px;
  margin-top: -2px;
}

.catalog__price {
  margin-right: 106px;
  margin-left: 28px;
  position: relative;
  margin-bottom: 0;
  font-size: 15px;
  line-height: 24px;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: -104%;
    background-image: url("../img/divider.svg");
    width: 204px;
    height: 1px;
  }
}

.catalog__price-medium-cart {
  margin-right: 119px;
}

.catalog__item-title-big {
  margin-top: 20px;
  margin-bottom: 9px;
}

@media (min-width: 768px) and (max-width: 1149px) {
  .catalog__title {
    padding-top: 101px;
    padding-bottom: 75px;
    font-size: 43px;
    font-weight: 50px;
    padding-left: 183px;
    padding-right: 183px;
    margin-bottom: 67px;
    position: relative;

    &::before {
      position: absolute;
      display: block;
      content: "";
      background-image: url("../img/zigzag-fill.svg");
      height: 24px;
      background-position: center;
      background-repeat: repeat-x;
      top: 93%;
      left: 0;
      z-index: 5;
      width: 100%;
    }
  }

  .catalog__item {
    width: 670px;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .catalog__item-title {
    font-size: 20px;
    line-height: 24px;
  }

  .catalog__subtitle {
    margin-left: 3px;
    margin-right: 71px;
    margin-top: 5px;
    margin-bottom: 0;
  }

  .catalog__price-cart {
    display: flex;
    align-items: center;
    white-space: nowrap;
    gap: 61px;
  }

  .catalog__item:not(:last-child) {
    margin-bottom: 51px;
  }

  .catalog__desc {
    margin-bottom: 183px;
    font-size: 15px;
  }

  .catalog__price {
    font-size: 17px;
  }

  .catalog__cart {
    margin-top: 10px;
  }

  .catalog__desc-big-cart {
    margin-bottom: 159px;
  }

  .catalog img {
    height: auto;
    object-fit: contain;
    display: block;
  }
}

@media (min-width: 1150px) {
  .catalog__title {
    font-size: 53px;
    font-weight: 57px;
    padding-top: 101px;
    padding-bottom: 96px;
    padding-left: 331px;
    padding-right: 331px;
    position: relative;

    &::before {
      position: absolute;
      display: block;
      content: "";
      background-image: url("../img/zigzag-fill-desktop.svg");
      height: 34px;
      background-position: center;
      background-repeat: repeat-x;
      top: 93%;
      left: 0;
      z-index: 5;
      width: 100%;
    }
  }

  .catalog__list {
    display: grid;
    grid-template-columns: 316px 316px 316px;
    width: 1050px;
    margin-top: 123px;
    margin-left: -1px;
    gap: 51px;
  }

  .catalog__item-title {
    font-size: 20px;
    line-height: 24px;
    margin-left: 28px;
    margin-top: 24px;
  }

  .catalog__desc {
    font-size: 15px;
    line-height: 30px;
    margin-bottom: 52px;
  }

  .catalog__price {
    font-size: 17px;
    line-height: 30px;
    margin-top: 6px;
    margin-right: 150px;

    &::before {
      top: -74%;
    }
  }

  .catalog__price-medium-cart {
    margin-right: 160px;
  }

  .catalog__desc-big-cart {
    margin-bottom: 28px;
    margin-top: -7px;
  }

  .catalog__subtitle {
    margin-bottom: 19px;
  }
}
